.profile {
    position: relative;
    width: 312px;
    height: 412px;
    background-color: #e7ecf2;
    margin-bottom: 40px;
    border-radius: 5px;
    box-shadow: -3px 5px 12px -3px rgba(0, 0, 0, 0.75);
}

.description {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20%;
}

.avatar {
    border-radius: 50%;
}

.stats {
    position: absolute;
    bottom: 0;
    display: flex;
    justify-content: space-between;
    width: 100%;
    margin: 0;
    padding: 0;
}

.statsItem {
    width: calc(100% / 3);
    display: flex;
    flex-direction: column;
    align-items: center;
    border: solid 1px grey;
}

.label {
    margin-bottom: 10px;
}
